.cb__grid {
    @include gridContainer; }

@for $i from 1 through 12 {
    .col-xs--#{$i} {
        grid-column: span $i; } }

@include phone {
    @for $i from 1 through 12 {
        .col-sm--#{$i} {
            grid-column: span $i; } } }

@include tablet {
    @for $i from 1 through 12 {
        .col-md--#{$i} {
            grid-column: span $i; } } }

@include laptop {
    @for $i from 1 through 12 {
        .col-lg--#{$i} {
            grid-column: span $i; } } }

@include desktop {
    @for $i from 1 through 12 {
        .col-xl--#{$i} {
            grid-column: span $i; } } }
