// Global mixins for sass
// to use in file use @include mixinName

@mixin gridContainer() {
    display: grid;
    grid-template-columns: repeat( 12, 1fr );
    grid-gap: 1rem; }

// media queries
@mixin phone() {
    @media screen and ( min-width: 425px  ) {
        @content; } }

@mixin tablet() {
    @media screen and ( min-width: 768px ) {
        @content; } }

@mixin laptop() {
    @media screen and ( min-width: 1024px ) {
        @content; } }

@mixin desktop() {
    @media screen and ( min-width: 1440px ) {
        @content; } }
